import { AvatarIcon as Avatar } from '@wag/icons-react';

export interface AvatarProps {
  size?: 'xl' | 'lg' | 'md' | 'sm';
  fill?:
    | 'red'
    | 'charcoal'
    | 'white'
    | 'info'
    | 'success'
    | 'warning'
    | 'error'
    | 'gray';
}

export const AvatarIcon = ({ size = 'lg', fill = 'charcoal' }: AvatarProps) => {
  return <Avatar size={size} fill={fill} />;
};
